@import url(https://cdn.jsdelivr.net/npm/the-new-css-reset@1.7.2/css/reset.min.css);
@import url(https://cdn.jsdelivr.net/npm/bulma@0.9.4/css/bulma.min.css);
.todo-list{display:grid;grid-template-columns:repeat(auto-fill, 500px);justify-content:center;grid-gap:22px;gap:22px;padding:10px}.todo-list__item{display:flex;flex-direction:column;grid-gap:10px;gap:10px;padding:30px;border-radius:20px;background-color:#fefefe;transition:box-shadow 200ms, -webkit-transform 200ms;transition:transform 200ms, box-shadow 200ms;transition:transform 200ms, box-shadow 200ms, -webkit-transform 200ms}.todo-list__item:hover{-webkit-transform:scale(1.05);transform:scale(1.05);box-shadow:0 0 20px 10px rgba(0,0,0,0.05)}.todo-list__divider{height:1px;background:#dcdcdc}@media (min-width: 1600px){.todo-list{grid-template-columns:repeat(3, 500px)}}

.todo-info{display:flex;justify-content:space-between;grid-gap:10px;gap:10px;font-weight:500}.todo-info__button{cursor:pointer}

.user-info{display:flex;align-items:center;grid-gap:10px;gap:10px;font-size:14px}.user-info__email{margin-left:auto}

body{font-size:16px;font-family:"Finlandica", Arial, sans-serif}.app{height:100vh;color:#282a2d;background-color:#e5e9f1}.app__title{font-size:32px;font-weight:bold;text-align:center}.app__form{display:flex;flex-direction:column;grid-gap:10px;gap:10px;max-width:510px;margin:30px auto}.app__input-group{position:relative;display:flex;justify-content:space-between;grid-gap:30px;gap:30px}.app__label-text{display:block;width:185px}.app__input-error{position:absolute;-webkit-transform:translateY(100%);transform:translateY(100%);font-size:14px}

